getCommentData(){
let _this=this;
var CommentData = Bmob.Object.extend("Comment");
var query = new Bmob.Query(CommentData);
query.limit(_this.dataNumber);
query.skip(_this.skip);
query.equalTo("shuoId", _this.id);
// 
query.find({
success: function(results) {
_this.skip+=_this.dataNumber;
_this.httpNum++;
_this.moreLoading=true;     //loding
if(results.length==0 && _this.commentArr.length!=0){
Toast(
{
message: '',
position: 'center',
duration: 2000
}
);
_this.skip=_this.commentArr.length;
}
// 
for (var i = 0; i < results.length; i++) {
var object = results[i];
var data={
commentfont: results[i].get("commentfont"),   //
sponsorAccid:results[i].get("sponsorAccid"),    //accid
sponsorName: results[i].get("sponsorName"),   //
sponsorGender:results[i].get("sponsorGender"),    //
sponsorPortrait:results[i].get("sponsorPortrait"),   //
receiveAccid:results[i].get("receiveAccid"),      //accid
receivename:results[i].get("receivename"),    //
