huadong(){
let _this=this;
var scrollTop;
if(_this.scrollArr.length>=100){
_this.scrollArr.length=0
}
if(document.documentElement&&document.documentElement.scrollTop)
{
scrollTop=document.documentElement.scrollTop;
_this.scrollArr.push(scrollTop);

}
else if(document.body)
{
scrollTop=document.body.scrollTop;
_this.scrollArr.push(scrollTop);
};

if(scrollTop>80 && _this.bottomOff==false){       //header
var le=_this.scrollArr.length;
if(_this.scrollArr[le-1]>=_this.scrollArr[le-2]){
_this.animateClass="header animated fadeOutUp";     //header
_this.animatedShuruClass="shuru animated fadeOutDown";     //
};
if(_this.scrollArr[le-1]<_this.scrollArr[le-2]){
_this.animateClass="header animated fadeInDown";      //
_this.animatedShuruClass="shuru animated fadeInUp";      //

};
}else if(scrollTop<80 && _this.animateClass=="header animated fadeOutUp"){   //headerheaderfadein
_this.animateClass="header animated fadeInDown";      //
_this.animatedShuruClass="shuru animated fadeInUp";      //
}else if(_this.bottomOff==true){
console.log("")
_this.animateClass="header animated fadeInDown";      //
_this.animatedShuruClass="shuru animated fadeInUp";      //
}
},
//
getDocumentHeight(){
let _this=this;
function getScrollTop(){
var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
if(document.body){
bodyScrollTop = document.body.scrollTop;
}
if(document.documentElement){
documentScrollTop = document.documentElement.scrollTop;
}
scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
return scrollTop;
}
//
function getScrollHeight(){
var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
if(document.body){
bodyScrollHeight = document.body.scrollHeight;
}
if(document.documentElement){
documentScrollHeight = document.documentElement.scrollHeight;
}
scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
return scrollHeight;
}
//
function getWindowHeight(){
var windowHeight = 0;
if(document.compatMode == "CSS1Compat"){
windowHeight = document.documentElement.clientHeight;
}else{
windowHeight = document.body.clientHeight;
}
return windowHeight;
}

window.onscroll = function(){
if(getScrollTop() + getWindowHeight()+80 >= getScrollHeight()){
_this.bottomOff=true;
}else{
_this.bottomOff=false;
};

};
},

$nextTick(){
